let total = vals.length

export default function Helper() {
    const [numDone, setNumDone] = useState(0)
    useHotkeys('cmd+c', e => {
        e.preventDefault()
        navigator.clipboard.writeText(vals.shift())
        setNumDone(numDone => numDone + 1);
    })
    return (
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '100vh', fontSize: '5rem' }}>
            <h1>{numDone} / {total}</h1>
        </div>
    )
}
